/* The game field - everething */
.field
{
  display:inline-flex;
}

/* Each game cell */
.cell
{
  border:5px solid grey;
  width:120px;
  height:120px;
  text-align:center;
  font-weight:700;
  font-size:4em;
}

/* Score and best bars, New game bar */
.score, .best, .new-game
{
  background:#b9b4af;
  min-width:150px;
  height:80px;
  margin-top:.2em;
  margin-left:1em;
  text-align:center;
  font-weight:700;
  font-size:1.5em;
}
.best
{
   margin-left:.1em;
}
.score-points, .best-points
{
  color:white;
}
.new-game
{
  padding-top:1em;
  height:55px;
  position:relative;
  top:100px;
  left:-325px;

}
a
{
  text-decoration:none;
  color:#2f1f1f
}
.game-over, .game-won
{
  position:relative;
  left:-14.7em;
  top:8.5em;
  font-size:2em;
  font-weight:700;
  min-width:500px;
  color:red;
}

.game-won
{
  color:green;
}


